.flexContainerIcons {
    display: flex;
    justify-content : center;
}
.flexContainerIcons > div {
    margin: 5px 4%;
    padding: 5px;
    font-size: 30px;
  }

.aboutUsDiv {
    background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
}

.divider2 {
    background-color: white;
    margin : 0px 40px;
    opacity : 0.5;
}

.asHeadingDiv {
    display: flex;
    justify-content: flex-start;
    padding : 0 15%;
    align-items: center ;
    color : white;
}
.asHeading2Div {
    display: flex;
    justify-content: flex-start;
    padding : 0 7%;
    align-items: center;
    font-family: 'Quicksand', sans-serif;
    color : #333333;
}

.labelDiv {
    display: flex;
    justify-content: flex-start;
    padding : 12px;
    align-items: center;
    font-family: 'Quicksand', sans-serif;
    font-size: 90%;
    color : whitesmoke;
}

.errorMessage {
    display: flex;
    justify-content: center;
    font-size: 70%;
    color : rgb(255, 0, 0);
    background-color: rgb(255, 249, 249);
    border-radius: 4px;
    margin : 0 15px;
    padding : 4px 0;
    font-family: 'Quicksand', sans-serif;
    
}

.nodata {
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 75px 30px;
}
.nodataHeading {
    font-weight: 900;
    font-size: x-large;
    padding: 20px 0;
}
.nodataDetailPara {
    font-size: 90%;
    color: teal;
}